.header{
    display: flex;
    justify-content:space-between;
    background: rgb(2,0,36);
    background: linear-gradient(90deg, rgba(2,0,36,1) 0%, rgba(125,60,148,1) 0%, rgba(80,81,225,1) 100%);
    color:white;
    height:65px;
    font-family: "Comic Sans MS", "Comic Sans", cursive;
}

.logo-and-title{
    display: flex;
}

.logo-title{
    font-size: 25px;
    font-weight:bold;
    margin:auto;
    padding-left: 10px;
}

.logo{
    width:45px;
    height:45px;
    padding:5px;
}

.project-name{
    font-size: 20px;
    margin:auto;
    margin-left:0;
    margin-right:10px;
}

@media (max-width:500px){
    .header{
    width:175vw;
    }   

    .logo-and-title{
        margin-right: 10px;
    }
}
.body{
    margin: 5% 20% 5% 20% ;  
}


.top-text-1, .bottom-text-1{
    position: absolute;
    color: azure;
    z-index: 5;
    font-size:50px;
    -webkit-text-stroke: 1px black;
    font-family: Impact, Haettenschweiler, 'Arial Narrow Bold', sans-serif;
    cursor:move;
}

.bottom-text-1{
    top:500px;
}

.form{
    display:grid;
    grid-template-columns: auto auto;
    grid-column-gap:15px;
    -webkit-column-gap:15px;
            column-gap:15px;
    margin:auto;
}

.form > *{
    font-size:large;
    margin:5px;
    height: 50px;
    border-radius: 4px;
    border:2px solid;
    border-image-slice: 1;
    border-image-source:linear-gradient(90deg, rgba(2,0,36,1) 0%, rgba(125,60,148,1) 0%, rgba(80,81,225,1) 100%);
}

input[type="text"]{
    padding-left: 10px;
}

.submit{
    color:white;
    background: rgb(2,0,36);
    background: linear-gradient(90deg, rgba(2,0,36,1) 0%, rgba(125,60,148,1) 0%, rgba(80,81,225,1) 100%);
    grid-column: 1 / span 2;
}

.submit:hover{
    cursor: pointer;
}

#main-image{
    width: 100%;
    object-fit: contain;
    max-height: 550px;
}

@media(max-width:500px){
    .form{
        display: block;
        width: 0vw;
    }
    .submit{
        width: -webkit-max-content;
        width: max-content;
        margin-left: 15px;
    }
    #main-image{
        width:130vw 
    }
}
